/*      /  */
body {
  width: 100%;
  max-width: 100%;
  margin: 0;
  font-family: -apple-system, Poppins, inter, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.h1-header2 {
  color: #ffffff;
}

:lang(ko) {
  font-family: 'Noto Sans KR', sans-serif;
}

/*   box-sizing  */
* {
  box-sizing: border-box;
}

/*    */
.section {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 75px;
  margin: 0 auto;
  flex-wrap: wrap; /*     */
}

/*      */
.hero-text {
  margin-top: 150px;
  font-family: Poppins;
  font-size: 70px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 70px */
  letter-spacing: -4px;
  width: 760px;
  max-width: 760px;
}

.h1-header {
  background: linear-gradient(263deg, #f77705 21.05%, #f79c05 77.63%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
h2 {
  font-size: 2.5rem;
  font-weight: 600;
  text-align: center;
  margin-bottom: 20px;
}

p {
  color: #fff;
  font-size: 24px;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  line-height: 28px; /* 116.667% */
}

