/* Section 1: Hero */
.hero-section {
  display: flex;
  justify-content: space-between;
  background: linear-gradient(303deg, #4f46e5 13.1%, #0711d9 91.43%);
  positon: relative;
  overflow: hidden;
  gap: 0px;
}
.hero-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
}
.hero-wrap {
  position: relative;
  width: 700px;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero-bg {
  position: absolute;
  inset: -20%;
  background-size: cover;
  background-position: center;
  transform: scale(1.1);
  border-radius: 50%;
  filter: blur(80px);
  object-fit: cotain;
  -webkit-mask-image: radial-gradient(circle, black 40%, transparent 80%);
  mask-image: radial-gradient(circle, black 40%, transparent 80%);
  z-index: 0;
}
.hero-bg::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 0.5;
  border: none;
  object-fit: contain;
  background: radial-gradient(
    circle at center,
    #f58524 0%,
    #dfec26 100%,
    transparent 100%
  );
}
.hero-wrap img {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-content p {
  max-width: 760px;
  margin-bottom: 70px;
  margin-top: 20px;
  font-size: 1rem;
}
.docs {
  cursor: pointer;
}

.link {
  padding-top: 44.4px;
  padding-bottom: 44.4px;
  color: #fff;
}
.toServiceButton {
  display: flex;
  width: 395px;
  height: 80px;
  padding: 30px 70px;
  justify-content: center;
  align-items: center;
  gap: 7.2px;
  color: #fff;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%; /* 18px */
  text-transform: uppercase;
  background: #f77704;
}

.hero-content button {
  background: linear-gradient(90deg, #f77705 0%, #f79c05 100%);
  color: #ffffff;
  border: none;
  font-weight: bold;
  cursor: pointer;
}

.hero-content button:hover {
  background: linear-gradient(90deg, #00e1ff60 0%, #f79c05 100%);
}

.slider-container {
  width: 100%; /*     */
  overflow: hidden; /*      */
  position: relative; /*   */
  padding: 65px 0 65px 0;
}

.slider-track {
  display: flex;
  gap: 150px; /*   40px   .     . */
  width: fit-content; /*         */
  animation: slide-animation 30s linear infinite; /*   */
  /* 30s  .      . */
  /* linear:    */
  /* infinite:   */
}

.slider-track img {
  flex-shrink: 0; /*     */
  width: 130px; /*  80px  */
  height: 130px; /*       */
  display: block; /*     */
}

/*       () */
@media (max-width: 1200px) {
  .slider-track img {
    width: calc(100vw / 4); /* 1200px  4  */
  }
}

@media (max-width: 768px) {
  .slider-track img {
    width: calc(100vw / 3); /* 768px  3  */
  }
}

@media (max-width: 480px) {
  .slider-track img {
    width: calc(100vw / 2); /* 480px  2  */
  }
}

@keyframes slide-animation {
  0% {
    transform: translateX(0); /*   */
  }
  100% {
    /*  15 +  5 =  20  */
    /* 5       5  */
    /* (15   5 ) */
    /*     (15)    */
    /*  (   /    ) * 100%   */
    /* (15 / 5) = 3 -> -300% (    1/3 ) */
    /*  ,  15     . */
    /*      * 15 (  )   */
    transform: translateX(calc(-1 * (100vw / 5) * 15));
    /* : 5    ,   15 , */
    /* 15       . */
    /* 100vw / 5   . */
  }
}

/*       ( ) */
.slider-track:hover {
  animation-play-state: paused;
}

/* Section 2: Analyze */
.analyze-section {
  background: #f4f4f4;
  flex-direction: column;
}

/*   (    ) */
.gradient-text-default,
.gradient-text-precision {
  background-clip: text !important;
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent !important;
  display: inline-block; /* span  background-clip  */
  white-space: nowrap; /*   */
  text-align: center;
  font-family: Poppins;
  font-size: 50px;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 50px */
  text-transform: capitalize;
}

/*     */
.gradient-text-default {
  background: linear-gradient(264deg, #f77705 16.11%, #f79c05 77.7%);
  /*       . */
}

/* "Precision"   */
.gradient-text-precision {
  background: linear-gradient(262deg, #4f46e5 31.59%, #0711d9 61.58%);
  /*       . */
}

.gradient-text {
  color: #121139;
  text-align: center;
  font-family: Poppins;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; /* 28.6px */
  letter-spacing: 0.88px;
  margin: 0 auto;
  margin-bottom: 120px;
}

.dashboard-image {
  width: 100%;
  max-width: 960px;
  max-height: 540px;
  aspect-ratio: 16 / 9; /* 16:9   */
  margin: 0 auto; /*  div      */
  margin-bottom: 180px;

  /* ---       --- */
  display: flex; /* Flexbox  . */
  justify-content: center; /* ()   */
  align-items: center; /* ()   */
  /* ------------------------------------- */
}

/*   margin: auto;   . */
.dashboard-image img {
  /*        */
  width: 960px; /*    */
  height: auto; /*   */
  object-fit: contain; /*        */
}

/* Section 3: Value */
.value-section {
  background: linear-gradient(303deg, #4f46e5 13.1%, #0711d9 91.43%);
  flex-direction: row;
  display: flex;
  padding-top: 125px;
  justify-content: center;
  align-items: center;
  gap: 100px;
}

.value-content-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  gap: 20px;
  margin: 0 auto 200px auto; /*    */
  flex-wrap: wrap; /*     */
}
.value-image {
  flex: 1;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  display: flex; /*     Flexbox 
    object-fit: cover; /*    */
}

.value-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  flex: 1;
  color: #fff;
  font-size: 20px;
  font-style: normal;
  max-width: 1000px;
  font-weight: 500;
  line-height: 130%; /* 28.6px */
  letter-spacing: 0.8px;
  min-width: 680px; /*    */
}

.value-content h2 {
  text-align: left;
  font-size: 30px;
  font-weight: 600;
  text-transform: capitalize;
  line-height: 100%;
  padding-bottom: 25px;
}

/* Section 4: Key Features */
.key-features-section {
  align-items: center;
  flex-direction: column;
  background: #f4f4f4;
  padding-top: 150px;
  position: relative;
  padding-bottom: 200px;
}

.key-features-description {
  color: #121139;
  text-align: center;
  font-family: Poppins;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; /* 28.6px */
  letter-spacing: 0.88px;
  margin: 45px auto;
}
.features-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
  padding: 100px 50px 0 50px;
}

.features-main-image {
  flex: 2;
  height: auto; /*      */
  display: flex;
  justify-content: center;
  align-items: center;
}

.features-main-image img {
  max-width: 100%; /*  (features-main-image)   */
  height: auto; /*   */
  display: block; /* margin auto      */
  margin: 0 auto; /*      (  ) */
  object-fit: contain; /*        */
}

.features-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 55px;
  text-aling: center;
}

/*    */
.features-mobile-image {
  position: absolute; /*  .features-main-image   */
  top: 55%; /*   */
  left: 50%; /*   */
  transform: translate(0%, 0%) translate(0%, -50%); /*     */
  z-index: 10; /*      z-index  */
  height: auto; /*    */
}
@media (max-width: 1400px) {
  .features-mobile-image {
    top: 60%; /*   */
    left: 50%; /*   */
    transform: translate(0%, 0%) translate(0%, -50%); /*     */
  }
  .features-mobile-image img {
    width: 150px; /*        (  ) */
    height: auto; /*    */
  }
}

/* features-mobile-image  img  */
.features-mobile-image img {
  height: auto; /*   */
  object-fit: contain; /*        */
  width: 300px;
}

.feature-item {
  color: #fff;
  font-family: Poppins;
  font-size: 20px;
  font-style: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  line-height: 130%; /* 26px */
  letter-spacing: 0.8px;
  border: 1px solid #ffffff;
  height: 120px;
  width: 100%;
  padding: 0 20px 0 20px;
  border-radius: 30px;
  background: #f77705;
  z-index: 1; /*      */
}

/* Section 5: AI Reports */
.ai-reports-section {
  background: linear-gradient(303deg, #4f46e5 13.1%, #0711d9 91.43%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  text-align: center;
}

.ai-reports-description {
  color: #fff;
  text-align: center;
  font-family: Poppins;
  font-size: 22px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; /* 28.6px */
  letter-spacing: 0.88px;
  margin: 50px auto 70px auto; /*    */
}

.report-content {
  display: grid;
  grid-template-columns: 1fr minmax(40%, 60%) 1fr;
  width: 100%;
  margin: 30px auto 100px auto;
  align-items: center;
  justify-content: center;
  position: relative;
}
.report-main-image {
  grid-column: 2;
  position: relative;
  justify-self: center;
  align-items: center;
  width: 100%;
  height: 450px;
  margin-bottom: 30px;
}
.report-content .feature-item {
  grid-column: 1;
  align-items: center;
  justify-self: end;
  transform: translateX(-5%);
}
.report-features {
  grid-column: 3;
  display: flex;
  flex-direction: column;
  gap: 10vw;
}

