/*            */
/* =================================== */

@media (max-width: 1024px) {
  h1 {
    font-size: 2.5rem;
  }
  h2 {
    font-size: 2rem;
  }
  .section {
    flex-direction: column;
    padding: 60px 5%;
  }
  .hero-image {
    width: 50%; /*      */
    height: auto; /*   */
  }
  .hero-section {
    text-align: center;
    position: relative;
    overflow: hidden;
  }
  .hero-content {
    align-items: center;
    display: flex;
    flex-direction: column;
  }

  .hero-wrap {
    display: none;
  }

  .analyze-section {
    padding-bottom: 0px;
  }

  .value-section {
    padding-top: 100px;
    padding-bottom: 0px;
    flex-direction: column; /*      */
    text-align: center;
  }
  .value-content h2 {
    text-align: center;
  }

  .footer {
    flex-direction: column;
    text-align: center;
  }

  .dashboard-image img {
    width: 700px; /*      */
    height: auto; /*   */
    margin: 0 auto;
    aspect-ratio: 16 / 9; /* 16:9   */
    margin-bottom: 180px;
    object-fit: cover; /*    */
  }
  .value-content-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 130px;
    margin: 0 auto 320px auto; /*    */
  }

  .features-main-image {
    flex: auto; /* Flex   */
    width: 90%; /*     */
    max-width: 500px; /*    */
    height: auto; /*    */
  }

  .features-list {
    flex: auto; /* Flex   */
    width: 90%; /*     */
    max-width: 400px; /*    */
    gap: 15px; /*    */
  }

  .feature-item {
    font-size: 18px; /*     */
    height: 100px; /*    */
  }
  .features-content {
    flex-direction: column; /*    */
    align-items: center;
    gap: 40px;
  }

  /*  features-mobile-image      */
  .features-mobile-image {
    position: relative; /*  absolute     */
    top: auto;
    left: auto;
    transform: none; /* transform  */
    order: 3; /*      ( ) */
    margin-top: 50px; /*    */
    max-width: 250px; /*    */
  }
  .report-content {
    grid-template-columns: 1fr;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 50px;
    margin: 0 auto;
  }
  .feature-left,
  .report-features,
  .report-main-image {
    width: 100%;
    transform: none;
    justify-content: center;
    align-items: center;
    gap: 40px;
    padding-bottom: 50px;
  }
  .feature-item,
  .report-features .feature-item,
  .report-content .feature-item {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    transform: none;
  }
}
