/*     */
.profile-clip-root {
  /* 16px  , 60px   */
  padding: 1rem 3.75rem; 
}

.profile-clip-page-container{
  display: flex;
  flex-direction: column;
  padding-left: 3.75rem;
  padding-right: 3.75rem;
  
}

/*  ( +  ) */
.profile-clip-header {
  display: flex;
  align-items: center;
  gap: 0.75rem; /* 12px */
  margin-bottom: 0.75rem; /* 12px */
}
.profile-clip-title {
  display: flex;
  align-items: center;
  gap: 0.75rem; /* 12px */
}
.profile-clip-meta {
  opacity: 0.7;
}

/*       */
.profile-clip-list {
  width: 100%;
}
.profile-clip-list .clip-row {
  width: 100%;
  max-width: 100%;
}

/*        */
.error-header {
  margin-bottom: 0.5rem; /* 8px */
  font-weight: 700;
}
.error-details {
  font-size: 0.75rem; /* 12px */
  opacity: 0.8;
}
.error-actions {
  margin-top: 0.75rem; /* 12px */
}

/*       */
.empty {
  padding: 1.5rem; /* 24px */
}

/* ... ( clip-page-container    ) ... */

.clip-page-header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 1rem;
  border-bottom: 1px solid #333;
}

.matchup-header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1.5rem; /*    (1rem -> 1.5rem) */
  width: 100%;
}

.team-display {
  display: flex;
  align-items: center;
  gap: 1rem; /*    (0.75rem -> 1rem) */
}


.team-name {
  font-size: 1.5rem; /*     (1.125rem -> 1.5rem, 24px) */
  font-weight: bold;
  color: #e0e0e0;
}

.vs-text {
  font-size: 1.25rem; /*     (1rem -> 1.25rem, 20px) */
  font-weight: bold;
  color: #888;
}