:root {
    --sidebar-width: 300px;
} /*       */

/* ------  ------ */
.serviceSidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: var(--sidebar-width);
    height: 100vh;
    background: #0f1012;
    overflow-y: auto; /*  () */
    z-index: 100; /*   */
}

/* ------   ------ */
.serviceLayoutContainer {
    padding-left: var(--sidebar-width); /*    */
    min-height: 100vh;
    display: flex; /*  header/  */
    flex-direction: column;
}

/* ------ main () ------ */
.serviceLayoutContainer > main {
    flex: 1;
    display: flex;
    flex-direction: column;
    min-width: 0;
}

/*       100% */
main > .serviceHeader {
    flex-shrink: 0; /*   */
    width: 100%;
    height: 56px; /*   */
    /*   */
    background: #141414;
    border-bottom: 1px solid #1f2023;
}

/*        */
main > .routesBody {
    flex: 1; /*    */
    overflow-y: auto; /* () */
}

.flex-1 {
    flex: 1; /*     */
    background-color: #141414;
}
