/* FAQModal.css */

/*    */
.modal-faq-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  padding: 1rem;
}

.modal-faq-card {
  position: relative; /*       */
  width: 100%;
  max-width: 1000px;
  max-height: 90vh;
  background-color: transparent; /*       */
  border-radius: 20px;
  overflow-y: auto; /*    */
}

/*   */
.modal-faq-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  color: white;
  font-size: 2.5rem;
  cursor: pointer;
  z-index: 10;
  line-height: 1;
}

/*    contact.css FAQ   ( )  */

/*  .faqContent  */
.modal-faqContent {
  /*         . */
  background-image: url('../assets/images/png/ContactPng/FAQheader.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  padding: 50px 20px;
  text-align: center;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

/*  .faqContent::before  */
.modal-faqContent::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(2, 14, 177, 0.397);
  z-index: 1;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.modal-faqContent h2,
.modal-faqContent p {
  position: relative;
  z-index: 2;
  color: #fff;
}

.modal-faqContent h2 {
  margin: 5px;
}
.modal-faqContent p {
  font-size: 1em;
  margin: 0;
}

/*  .faqGrid  */
.modal-faqGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  background-color: #333;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  padding: 30px;
}

/*  .faqItem, .faqHeader   */
.modal-faqItem {
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 10px;
}
.modal-faqItem.expanded {
  background-color: rgba(255, 255, 255, 0.05);
}

.modal-faqHeader {
  display: flex;
  align-items: center;
  padding: 15px;
  cursor: pointer;
  position: relative;
}

.modal-faqNumber {
  font-size: 1.2em;
  font-weight: bold;
  margin-right: 15px;
  color: #fff;
  padding: 15px;
  transition: color 0.3s ease;
}
.modal-faqHeader.expanded .modal-faqNumber {
  color: #f77705;
}

.modal-faqHeader h4 {
  margin: 0;
  flex-grow: 1;
  text-align: left;
  color: #fff;
  transition: color 0.3s ease;
}
.modal-faqHeader.expanded h4 {
  color: #f77705;
}

.modal-faqHeader:hover .modal-faqNumber,
.modal-faqHeader:hover h4 {
  color: #f77705;
}

.modal-toggleIcon {
  font-size: 2em;
  line-height: 1;
  color: #fff;
  font-weight: 300;
  transition: transform 0.3s ease;
}
.modal-faqHeader.expanded .modal-toggleIcon {
  transform: rotate(
    45deg
  ); /*    + -> x    */
}

/*  .faqBody  */
.modal-faqBody {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out, padding 0.3s ease-out;
  padding-left: 85px;
  font-size: 10px;
}
.modal-faqBody.expanded {
  max-height: 200px;
  padding-bottom: 20px;
  padding-top: 0px;
}

.modal-faqBody p {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: left;
  color: #ffffff;
  margin: 0;
  font-size: 1.6em;
  width: 320px;
}

/*   */
@media (max-width: 768px) {
  .modal-faqGrid {
    grid-template-columns: 1fr;
  }
}
